.sidebar {
  position: absolute;
  width: 317px;
  height: 100vh;
  background-color: white;
  transform: translateX(-100%);
  transition: transform 0.3s ease;
}

.sidebar.open {
  transform: translateX(0);
  z-index: 1;
}

.background {
  background-color: gray;
}

.nav {
  display: flex;
  flex-direction: column;
  font-size: 30px;
  text-align: left;
  padding-left: 50px;
}

.nav a {
  text-decoration: none;
  padding-top: 20px;
  color: black;
  cursor: pointer;
}

.nav a:hover {
  text-decoration: underline;
  color: blue;
}

.nav a.active {
  text-decoration: underline;
  color: blue;
}

.close {
  margin-left: 90%;
  display: inline-block;
  border: none;
  padding-top: 10px;
  background-color: white;
}

.close img {
  border: none;
}
